const useStyles = makeStyles(theme => ({
    root: {
        backgroundColor: props => props.color,
        // width: '490px',
        borderRadius: '10px',
        fontFamily: 'SFProText',
        boxShadow: 'none',
        textTransform: 'capitalize',
        padding: '15px 20px',
        '&:hover': {
            backgroundColor: props => props.hover,
            boxShadow: 'none',
        },
        '&:focus': {
            backgroundColor: props => props.focus,
            boxShadow: 'none',
            outline: 'none'
        },
    },
}));



export default function CustomButton(props) {
    const propsObj = {
        className: props.className,
        ...props
    }
    const classes = useStyles(propsObj);
    const customClass = propsObj.className

    delete propsObj.className

    return <Button {...propsObj} variant="contained" color="" className={classes.root + ' ' + customClass} >
        {props.btntext}
    </Button>
}
