import React from 'react'

import Footer from '../../component/Footer'
import CustomButton from '../../component/CustomButton'

import { ColorScheme } from '../../enums'
import { useHistory } from "react-router-dom";
import { SvgIcons } from '../../icons'
import { useFormik } from 'formik'
import { Auth } from '../../validation'
import { AuthService } from '../../services'
import Txt from '../../component/Txt'

function GetUserByEmail(props) {
    const initState = {
        email: "",
    }

    let history = useHistory()
    function handleButton(props) {

        const payload = {
            email: formik.values.email
        }

        AuthService.getUserByEmail(payload.email).then(res => {
            console.log("User Exist ---> LogIn", res);

            history.push({
                pathname: '/login',
                state   : { email: payload.email }
            })

        }).catch(err => {
            console.log("User doesn't Exist ---> SignUp", err.response);

            history.push({
                pathname: '/signup',
                state   : { email: payload.email }
            })
        })
    }

    const formik = useFormik({
        initialValues   : { ...initState },
        onSubmit        : handleButton,
        validationSchema: Auth.LoginVld
    })

    return (
        <div id =   "Login">

            {/* <div className  =   "ml-100    mt-40     d-none     d-sm-block" >
                <SvgIcons.SnapThatHome />
            </div> */}

            <center>
                <div className  =   "main">

                    <div className  =   "mt-152">    <SvgIcons.Home />   </div>
                    <div className  =   "heading1   mt-15   mb-70">    Welcome Back.   </div>

                    <form onSubmit  =   {formik.handleSubmit}>

                        <Txt
                            icon        =   {<SvgIcons.Email/>}
                            placeholder =   "Email Address"

                            className   =   "mb-40"
                            name        =   "email"
                            value       =   {formik.values.email}
                            onChange    =   {formik.handleChange}
                            inputProps  =   {{ onFocus: formik.handleBlur }}
                            error       =   {formik.touched.email && formik.errors.email}
                            helperText  =   {formik.touched.email && formik.errors.email ? formik.errors.email : ''}
                        />

                        <CustomButton
                            color       =   {ColorScheme.ColorSchemeCode.cF2E9E1}
                            hover       =   {ColorScheme.ColorSchemeCode.cE4D0BF}
                            focus       =   {ColorScheme.ColorSchemeCode.cD4B59B}
                            className   =   "w-100   mb-20"
                            btntext     =   "Next"
                            type        =   "submit"
                        />

                    </form>
                    {/* <Link className =   "link">    Forgot Password?    </Link> */}

                </div>
                <Footer />
            </center>
        </div>
    )
}

export default GetUserByEmail
