import React from 'react'

import CustomButton from '../../component/CustomButton'
import Footer from '../../component/Footer'

import { ColorScheme } from '../../enums'
import { useHistory } from "react-router-dom";
import { SvgIcons } from '../../icons'
import { useFormik } from 'formik'
import { Auth } from '../../validation'
import { AuthService } from '../../services'
import Txt from '../../component/Txt'

function SignUp(props) {

    const email = props.location.state.email;

    const initState = {
        fullname    : "",
        phone       : "",
        password    : "",
    }

    let history = useHistory()
    function handleButton(props) {
        const payload = {
            fullname    : formik.values.fullname,
            email       : email,
            phone       : formik.values.phone,
            avatar      : "avatar",
            password    : formik.values.password
        }

        AuthService.SignUp(payload).then(res => {
            console.log("User Created Succesfully", res);

            history.push("/empty");
        }).catch(err => {
            console.log("SignUp Error", err.response);
        })

    }

    const formik = useFormik({
        initialValues   : { ...initState },
        onSubmit        : handleButton,
        validationSchema: Auth.SignUpVId
    })



    return (
        <div id     =   "Signup">

            {/* <div className  =   "ml-5    mt-4   pl-5     d-none     d-sm-block" >
                <SvgIcons.SnapThatHome />
            </div> */}

            <center>
                <div className  = "main">

                    <div className  =   "heading1    mt-104     mb-25">Create your Snap Account</div>
        <div className  =   "link mb-50">Signing up as <span className    =   "linkspan">{email}</span></div>

                    <form onSubmit  =   {formik.handleSubmit}>

                        <Txt
                            icon        =   {<SvgIcons.Profile/>}
                            placeholder =   "Full Name"

                            className   =   "mb-25"
                            name        =   "fullname"
                            value       =   {formik.values.email}
                            onChange    =   {formik.handleChange}
                            inputProps  =   {{ onFocus: formik.handleBlur }}
                            error       =   {formik.touched.fullname && formik.errors.fullname}
                            helperText  =   {formik.touched.fullname && formik.errors.fullname ? formik.errors.fullname : ''}
                        />

                        <Txt
                            icon        =   {<SvgIcons.Mobile/>}
                            placeholder =   "Phone"

                            className   =   "mb-25"
                            name        =   "phone"
                            value       =   {formik.values.phone}
                            onChange    =   {formik.handleChange}
                            inputProps  =   {{ onFocus: formik.handleBlur }}
                            error       =   {formik.touched.phone && formik.errors.phone}
                            helperText  =   {formik.touched.phone && formik.errors.phone ? formik.errors.phone : ''}
                        />

                        <Txt
                            icon        =   {<SvgIcons.Lock/>}
                            placeholder =   "Password"
                            type        =   "password"

                            className   =   "mb-25"
                            name        =   "password"
                            value       =   {formik.values.password}
                            onChange    =   {formik.handleChange}
                            inputProps  =   {{ onFocus: formik.handleBlur }}
                            error       =   {formik.touched.password && formik.errors.password}
                            helperText  =   {formik.touched.password && formik.errors.password ? formik.errors.password : ''}
                        />

                        <CustomButton
                            color       =   {ColorScheme.ColorSchemeCode.cF2E9E1}
                            hover       =   {ColorScheme.ColorSchemeCode.cE4D0BF}
                            focus       =   {ColorScheme.ColorSchemeCode.cD4B59B}
                            className   =   "w-100 "
                            btntext     =   "Sign Up"
                            type        =   "submit"
                        />
                    </form>
                </div>

                <Footer />

            </center>
        </div>
    )
}

export default SignUp
