import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import InputAdornment from '@material-ui/core/InputAdornment';

import { ColorSchemeCode } from '../enums/ColorScheme';

const useStylesElement = makeStyles(() => ({
  root: {
    border          : '1px solid ' + ColorSchemeCode.cBDBDBD,
    color           : "#828282",
    borderRadius    : 15,
    backgroundColor : "#F2F2F2",
    outlineColor    : "#F5F7F9",
    fontSize        : '16px',
    fontFamily      : 'SF Pro Text',
    '&:hover'   : {
      backgroundColor: "#F2F2F2",
    },
    '&$focused' : {
      backgroundColor: "#F2F2F2",
      borderColor    : "#394A66",
      outlineColor   : "#F5F7F9",
      color          : "#394A66"
    },
  },
  focused: {},
}));

const useStylesInputElement = makeStyles(() => ({
  root: {
    paddingLeft: '20px',
  }
}));

const useStylesLabelElement = makeStyles(() => ({
  root: {
    paddingLeft: '8px',
  }
}));

const InputAdorment = (props) => {
  return (
    <InputAdornment position  = {props.position}>
      {props.icon}
    </InputAdornment>
  )
}

export default function SearchTxt(props) {
  const classes      = useStylesElement();
  const inputClasses = useStylesInputElement();
  const labelClasses = useStylesLabelElement();
  const InputProps   = {
    classes,
    disableUnderline: true,
    startAdornment  : <InputAdorment position = "start" icon  = {props.icon} />,
  }

  return <TextField
    variant         = "outlined"
    autoComplete    =   "off"
    inputProps      ={{ className: inputClasses.root }}
    InputLabelProps ={{ className: labelClasses.root }}
    InputProps      ={InputProps}
    fullWidth
    {...props}

  />;
}



