import React from "react";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import PropertyType from "../component/PropertyType";
import PropertyDetail from "../component/PropertyDetail";
import PropertyImage from "../component/PropertyImage";
import SideBar from '../component/SideBar'
import Modal from "@material-ui/core/Modal";
import { makeStyles } from "@material-ui/core/styles";
import { useHistory } from "react-router-dom";



const useStyles = makeStyles((theme) => ({
  root        : {
    width         : "100%"
  },
  instructions: {
    marginTop     : theme.spacing(1),
    marginBottom  : theme.spacing(1),
  },
  paper: {
    position        : "absolute",
    width           : 400,
    backgroundColor : theme.palette.background.paper,
    boxShadow       : theme.shadows[5],
    padding         : theme.spacing(2, 4, 3),
    textAlign       : 'center',
    left            : '34.5%',
    top             : '20%' ,
    borderRadius    : '8px',
  }
}));

function getSteps() {
  return ["Property Type & Location", "Property Detail", "Add Images"];
}

function getStepContent(step) {
  switch (step) {
    case 0  :
      return <PropertyType/>;
    case 1  :
      return <PropertyDetail/>;
    case 2  :
      return <PropertyImage/>;
    default :
      return "Unknown step";
  }
}

export default function StepperAddProperty() {
  const classes = useStyles();
  const steps   = getSteps();

  const [activeStep , setActiveStep ]  = React.useState(0);
  const [skipped    , setSkipped    ]     = React.useState(new Set());

  const isStepSkipped = (step) => {
    return skipped.has(step);
  };

  const handleNext = () => {
    let newSkipped = skipped;

    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    setSkipped(newSkipped);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const [open, setOpen] = React.useState(false);
  
  let history = useHistory()
  const handleOpen = () => {
    setOpen(true);
    setTimeout(()=>{
      setOpen(false)
      history.push("/main");
    },2000)
  };

  const handleClose = () => {
    setOpen(false);
  };



  const body = (
    <div className={classes.paper}>
      <img src  = "/assets/images/confirm.gif" alt="gif"              style  = {{width:'252px',height:'252px'}}/>
      <div style  = {{fontFamily:'SF Pro Text',fontSize:'36px',color:'#394A66'}}>Confirmed</div>
      <div style  = {{fontFamily:'SF Pro Text',fontSize:'16px',color:'#ADABBA'}}>Your  property has been added.</div>
    </div>
  );

  return (
    <div id = "Stepper">
      <SideBar/>
      <div className  = {classes.root}>
        <div className  = "heading2       mt-32        ml-24">Add your property.</div>
        <div className  = "p      ml-24      mt-8        mb-16">Fill the following details to add your property.</div>
        <Stepper activeStep = {activeStep}>
        {steps.map((label, index) => {
          const stepProps   = {};
          const labelProps  = {};
          
          if (isStepSkipped(index)) {
            stepProps.completed = false;
          }
          return (
            <Step key = {label} {...stepProps}>
              <StepLabel {...labelProps}>{label}</StepLabel>
            </Step>
          );
        })}
      </Stepper>
        <div>
          {activeStep === steps.length ? (
            <div>
              
            </div>
          ) : (
            <div>
              <Typography className = {classes.instructions}>
                {getStepContent(activeStep)}
              </Typography>
              <div className  = "ml-24   mb-30  btn-main-div" >
                <Button
                  disabled  = {activeStep === 0}
                  onClick   = {handleBack}
                  className = "btn-previous   text-left "
                >
                  Previous
                </Button>
                {
                  (activeStep !== 2) ? 
                  <Button
                    variant   = "contained"
                    onClick   = {handleNext}
                    className = "btn-next"
                  >
                    Next
                  </Button>

                  :
                  <>
                  <Button
                    variant   = "contained"
                    onClick   = {handleOpen}
                    className = "btn-next"
                  >
                    Finish
                  </Button>
                  <div className="modal">
                    <Modal
                    open    = {open}
                    onClose = {handleClose}
                  >
                    {body}
                  </Modal>
                </div>
                </>
                  

                }
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
