import { BrowserRouter, Route, Switch } from 'react-router-dom'

import Chat from './pages/chat/chat';
import Login from './pages/auth/Login';
import SignUp from './pages/auth/SignUp';
import Details from './pages/Details';
import Sidebar from './pages/chat/component/Sidebar';
import Message from "../src/component/Message";
import DashboardMain from './pages/DashboardMain';
import GetUserByEmail from './pages/auth/GetUserByEmail';
import StepperAddProperty from './pages/Stepper';

import '../src/assets/css/global.scss';
import 'bootstrap/dist/css/bootstrap.min.css';
import DashboardEmpty from './pages/DashboardEmpty';

function App() {
  return (
    <BrowserRouter>
      <Switch>

        <Route component  = {GetUserByEmail}        exact path="/"            />{/*  */}
        <Route component  = {Login}                 exact path="/login"       />{/*  */}
        <Route component  = {SignUp}                exact path="/signup"      />{/*  */}
        <Route component  = {StepperAddProperty}    exact path="/stepper"     />{/*  */}
        <Route component  = {DashboardMain}         exact path="/main"        />{/*  */}
        <Route component  = {Details}               exact path="/details"     />{/*  */}
        <Route component  = {DashboardEmpty}        exact path="/empty"       />{/*  */}

        <Route component  = {Sidebar}               exact path="/Sidebar"     />
        <Route component  = {Chat}                  exact path="/chat"        />
        <Route component  = {Message}               exact path="/Message"     />

      </Switch>
    </BrowserRouter>
  );
}

export default App;

