import React from 'react'

import Footer from '../../component/Footer'
import CustomButton from '../../component/CustomButton'
// import CutomTextField from '../../component/CutomTextField'

import { ColorScheme } from '../../enums'
import { useHistory } from "react-router-dom";
import { SvgIcons } from '../../icons'
import { Link } from 'react-router-dom'
import { useFormik } from 'formik'
import { Auth } from '../../validation'
import { AuthService } from '../../services'
import Txt from '../../component/Txt'

function Login(props) {

    const email = props.location.state.email;

    const initState = {
        email   : email,
        password: "",
    }

    let history = useHistory()
    function handleButton(props) {
        const payload = {
            email   : email,
            password: formik.values.password
        }

        AuthService.Login(payload).then(res => {

            console.log("Successfully Logged In", res);

            history.push("/empty");
        }).catch(err => {
            console.log("LogIn Error", err.response);
        })

    }

    const formik = useFormik({
        initialValues   : { ...initState },
        onSubmit        : handleButton,
        validationSchema: Auth.LoginVld
    })



    return (
        <div id =   "Login">

            {/* <div className  =   "ml-100    mt-40     d-none     d-sm-block" >
                <SvgIcons.SnapThatHome />
            </div> */}

            <center>
                <div className  =   "main">

                    <div className  =   "mt-152">    <SvgIcons.Home />   </div>
                    <div className  =   "heading1   mt-15   mb-70">    Welcome Back.   </div>

                    <form onSubmit  =   {formik.handleSubmit}>

                        <Txt
                            icon        =   {<SvgIcons.Lock/>}
                            placeholder =   "Password"
                            type        =   "password"

                            className   =   "mb-40"
                            name        =   "password"
                            value       =   {formik.values.password}
                            onChange    =   {formik.handleChange}
                            inputProps  =   {{ onFocus: formik.handleBlur }}
                            error       =   {formik.touched.password && formik.errors.password}
                            helperText  =   {formik.touched.password && formik.errors.password ? formik.errors.password : ''}
                        />

                        <CustomButton
                            color       =   {ColorScheme.ColorSchemeCode.cF2E9E1}
                            hover       =   {ColorScheme.ColorSchemeCode.cE4D0BF}
                            focus       =   {ColorScheme.ColorSchemeCode.cD4B59B}
                            className   =   "w-100   mb-20"
                            btntext     =   "Sign In "
                            type        =   "submit"
                        />

                    </form>
                    <Link className    =   "link">    Forgot Password?    </Link>

                </div>
                <Footer />
            </center>
        </div>
    )
}

export default Login